* {
  box-sizing: border-box;
}

body {
  background-image: url(./image/—Pngtree—pink\ power\ glowing\ light\ bulb_1244200.jpg);
  background-size: cover;
}

ul,
ol {
  padding: 0;
  margin: 0;

  list-style: none;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}
